*{
    box-sizing: border-box;
}
.about-outer-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    max-height: 800px;
    text-align: center;
}
.about-container{
    height: 66vh;
    max-height: 800px;
    width: 96vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background: linear-gradient(270deg, #babee2, #9bcde4, #c3a3ea, #f5d59e);
    background-size: 800% 800%;
    border-radius: 10px;
    -webkit-animation: gradientAnimation 45s ease infinite;
    -moz-animation: gradientAnimation 45s ease infinite;
    animation: gradientAnimation 45s ease infinite;
    text-align: justify;
}

.about-left-side{
    width: 50%;
    height: 80%;
}

.about-title h2{
    font-weight: bold;
    letter-spacing: -3px;
}
.about-text{
    margin-top: 15px;
    margin-bottom: 10px;
}
.about-text p{
    letter-spacing: -0.8px;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
}

.about-text a{
    color: black;
    text-decoration: none;
    text-decoration: underline;
}

.about-text a:visited{
    color: black;
}

.about-right-side img{
    width: 350px;
    height: auto;
    border-radius: 8px;
}
.expertise{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.expertise img{
    margin: 5px;
    width: 24px;
    height: auto;
}

.expertise-desc{
    position: absolute;
    margin-top: 40px;
    padding: 0.55rem 1rem;
    font-size: 0.8rem;
    font-weight: 500;
    white-space: nowrap;
    color: #000;
    border-radius: 50px;
    background-color: #fff;
    box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
    pointer-events: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    opacity: 0;
    transition: all 0.2s ease-in-out;
}

.expertise .expertise-icons:hover  .expertise-desc{
    opacity: 0.9;
}
.expertise-icons{
    display: flex;
    flex-direction: row;
    cursor: pointer;
}

@-webkit-keyframes gradientAnimation {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes gradientAnimation {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes gradientAnimation {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}