.footer-outer-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.footer-text-container h2{
    font-size: 36px;
    font-weight: bold;
    letter-spacing: -2px;
    text-align: center;
}

.footer-links-container{
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 150px;
}

.footer-links-container a{
    margin-top: 50px;
    font-weight: 600;
    text-decoration: none;
    color: black;
    display: block;
    position: relative;
    overflow: hidden;
}

.footer-links-container a::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.1em;
    border-radius: 10px;
    background-color: black;
    opacity: 0;
    transition: opacity 300ms, transform 300ms;
  }
  

.footer-links-container a::after{
    opacity: 1;
    transform: translate3d(-100%, 0, 0);
    border-radius: 1000px;
}


.footer-links-container a:hover::after,
.footer-links-container a:focus::after{
    transform: translate3d(0, 0, 0);
}